import { assets } from '@/assets/assets'
import Image from 'next/image'
import React from 'react'


const Hero = () => {
  return (
    <div className='w-11/12 max-w-3xl text-center mx-auto h-screen flex flex-col items-center justify-center gap-4 pt-20'>
      <div>
        <Image className='rounded-full w-40' src={assets.profile_img} alt='my photo' />
      </div>
      <h3 className='flex item-end gap-2 text-xl md:text-2xl mb-3 font-Ovo'>
        Hi! I'm Azuh Ifeanyi Anderson<Image className='w-6' src={assets.hand_icon} alt='my photo' /></h3>
      <h1 className='text-3xl sm:text-6xl lg:text-[66px] font-Ovo'>
        A Frontend Web Developer.
      </h1>
      <p className='max-w-2xl mx-auto font-Ovo'>
        I am a frontend developer from Nigeria with 4 years of experience with a specialty in helping start up companies transcend their vision into reality.
      </p>

      <div className='flex flex-col sm:flex-row items-center gap-4 mt-4'>
        <a href="#contact" className='px-10 py-3 border border-white rounded-full bg-black text-white flex items-center gap-2'>
            Contact me <Image className='w-4' src={assets.right_arrow_white} alt='my contact' /></a>
        
        <a href="/AZUH CV.pdf" download className='px-10 py-3 border rounded-full border-gray-500 flex items-center gap-2'
        >My Resume <Image className='w-4' src={assets.download_icon} alt='my contact' /></a>
      </div>
    </div>
  )
}

export default Hero
