import React from 'react'
import { assets, workData } from '@/assets/assets'
import Image from 'next/image'




const Portfolio = () => {
  return (
    <div id='portfolio' className='py-4 mx-2 scroll-mt-20'>
      <h2 className='text-center text-5xl font-Ovo'>My Portfolio</h2>
      <p className='text-center max-w-2xl mx-auto mt-5 mb-12 font-Ovo'>
        Explore my collection of projects showcasing my expertise in frontend development.
      </p>

      <div className='grid grid-cols-auto my-10 gap-5'>
        {workData.map((project, index)=>(
          <div className='aspect-square bg-no-repeat bg-cover bg-center rounded-lg relative cursor-pointer group' key={index} style={{backgroundImage: `url(${project.bgImage})`}}>
            <div className='bg-white w-10/12 rounded-md absolute bottom-5 left-1/2 -translate-x-1/2 py-3 px-5 flex item-center justify-between duration-500 group-hover:bottom-8'>
              <div>
                <h2 className='font-semibold'>{project.title}</h2>
                <p className='text-sm text-gray-700 mb-5'>{project.description}</p>
                <a target='blank' href={project.link} className='px-5 py-2 border border-white rounded-full bg-blue-400 group-hover:bg-blue-700 text-white font-Ovo'>
                  Visit </a>
              </div>
            </div>    
          </div>
        ))}
      </div>
    </div>
  )
}

export default Portfolio
 