import { assets } from '@/assets/assets'
import Image from 'next/image'
import React, { useRef, useState } from 'react'
import emailjs from '@emailjs/browser'
import { toast } from 'react-toastify'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'



const Contact = () => {

  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [description, setDescription] = useState('')

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_f12ibxq', 'template_rkbs2k1', form.current, {
        publicKey: 'ttyPBYGuV6uPnnaEX',
      })
      .then(
        () => {
          setName('')
          setEmail('')
          setDescription('')
          toast.success('Message Sent Successfully!')
        },
        (error) => {
          toast.error('Message Failed to Send!')
        },
      );
  };

 
  return (
    <div id='contact' className='w-full px-[12%] py-5 scroll-mt-20 bg-[url("/footer-bg-color.png")] bg-no-repeat bg-center bg-[length:90%_auto]'>
        <h4 className='text-center mb-2 text-lg font-Ovo'>Connect with me</h4>
        <h2 className='text-center text-5xl font-Ovo'>Get in touch</h2>
        <p className='text-center max-w-2xl mx-auto mt-5 mb-12 font-Ovo'>
            Please reach out to me if you have any questions or feedback by filling the form below.
        </p>
        <form ref={form} onSubmit={sendEmail}>
        <div className='grid grid-cols-auto gap-6 mt-10 mb-8'>
                <input className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white' 
                type="text" 
                name="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder='Please enter name'
                required />

                <input className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white' 
                type="email" 
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                name="email"
                placeholder='Please enter email' 
                required/>
            </div>
          <textarea className='w-full p-4 outline-none border-[0.5px] border-gray-400 rounded-md bg-white mb-6' 
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            name="description"
            rows='5' 
            placeholder='Enter message' 
            required></textarea>
          <button className='py-3 px-8 w-max flex items-center justify-between gap-2 bg-black/80 text-white rounded-full mx-auto hover:bg-black duration-500 font-Ovo'
            type='submit' value='Send'>
            Submit <Image src={assets.right_arrow_white} alt='' className='w-4'/></button>

            <ToastContainer />

        </form>
    </div>
  )
}

export default Contact
