import { assets, infoList, toolsData } from '@/assets/assets'
import Image from 'next/image'
import React from 'react'

const About = () => {
  return (
    <div id='about' className='w-full px-[12%] py-5 scroll-mt-20'>
      <h2 className='text-center text-5xl font-Ovo'>About Me</h2>
        <div className='flex w-full flex-col lg:flex-row items-center gap-20 my-10'>
            <div className='w-64 sm:w-80 rounded-3xl max-w-none'>
                <Image className='w-full rounded-3xl' src={assets.profile_img} alt='' />
            </div>
            <div className='flex-1'>
                <p className='mb-10 max-w-2xl font-Ovo'>
                    I am a front end developer with high proficiency with React JS, Next JS would be very interested in working with you and your organization. 
                    My aim is to improve myself creating better interfaces and experiences for the user, making the web, 
                    application’s and technology easier for the user and simplifying communication barriers 
                    between the client and their users. 
                </p>
                <ul className='grid grid-cols-1 sm:grid-cols-3 gap-6 max-w-2xl'>
                    {infoList.map(({icon, iconDark, title, description}, 
                        index)=>(
                        <li className='hover:bg-lightHover hover:scale-105 
                            transition-all duration-500 hover:shadow-black border-[0.5px] border-gray-400 rounded-xl p-6 cursor-pointer' key={index}>
                            <Image src={icon} alt={title} className='w-7 mt-3'/>
                            <h3 className='my-4 font-semibold text-blue-500'>{title}</h3>
                            <p className=' text-blue-400 text-sm'>{description}</p>
                        </li>
                    ))}
                </ul>
                <h4 className='my-6 text-blue-500 font-Ovo'>My Frameworks</h4>
                <ul className='flex items-center gap-3 sm:gap-5'>
                    {toolsData.map((tool, index)=>(
                        <li className='flex item-center justify-center p-2 aspect-square border border-gray-400 rounded-lg cursor-pointer hover:scale-105 
                            transition-all duration-500' key={index}>
                            <Image src={tool} alt='' className='w-5 sm:w-7'/>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    </div>
  )
}

export default About
